<template>
  <div>
    <el-table
      :data="list"
      row-key="id"
      border
      style="width: 100%; margin-bottom: 20px"
      :tree-props="{ children: 'children' }"
    >
      <el-table-column prop="id" label="用户编号" width="180">
      </el-table-column>
      <el-table-column prop="username" label="用户名" width="180">
      </el-table-column>
      <el-table-column prop="rolename" label="所属角色" width="180">
      </el-table-column>
      <el-table-column label="状态" width="180">
        <template slot-scope="scope">
          <el-button type="primary" v-if="scope.row.status == 1"
            >启用</el-button
          >
          <el-button type="danger" v-else>禁止</el-button>
        </template>
      </el-table-column>
      <el-table-column label="操作" width="180">
        <template slot-scope="scope">
          <el-button type="primary" @click="edit(scope.row.uid)">编辑</el-button>
          <el-button type="danger" @click="del(scope.row.uid)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- 分页 -->
      <el-pagination  background layout="prev, pager, next" :page-size="size" :total="count" @current-change="cPage">
</el-pagination>
  </div>
</template>

<script>
import {requestDelManger} from '../../../utils/request'
import { mapActions, mapGetters } from "vuex";
import { successAlert, warningAlert } from '../../../utils/alert';
export default {
  computed: {
    ...mapGetters({
      'list': "manger/list",
      'count':'manger/count',
      'size':'manger/size',
      'page':'manger/page'
    }),
  },
  data() {
    return {};
  },
  methods: {
    ...mapActions({
        'requestCount':'manger/countActions',
      'requestMangerList': "manger/listActions",
      'requestPage': "manger/pageActions",
    }),
    cPage(page){
        this.requestPage(page)
        this.requestMangerList()
    },
    edit(id){
        this.$emit('edit',id)
    },
    del(id){
      requestDelManger({uid:id}).then(res=>{
        if(res.data.code==200){
          successAlert(res.data.msg)
        this.requestMangerList()

        }else{
          warningAlert(res.data.msg)
        }
      })
    }
  },
  mounted() {
      this.requestCount()
    this.requestMangerList();
  },
};
</script>

<style>
</style>


